main {
    min-height: 100%;
    height: auto;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    transition: var(--transition, 1s);
}

.content {
    flex: 1 1 auto;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.content div.wrapper {
    max-height: 90vh;
    max-width: 90vw;
}
.content div.wrapper img {
    max-height: inherit;
    max-width: inherit;
    border-radius: var(--size-great, 1rem);
    filter: drop-shadow(.5rem 1rem .5rem black);
}
.content > video {
    max-height: 90vh;
    max-width: 100%;
    padding: var(--size-titan, 2rem);
    border-radius: var(--size-great, 1rem);
    filter: drop-shadow(.5rem 1rem .5rem black);
}

div.cardsList {
    height: auto;
    width: 70%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
    background: rgba(218, 218, 218, .7);
    margin-top: auto;
    margin-bottom: 2rem;
    padding: 2rem;
    backdrop-filter: blur(var(--size-normal,.25rem));
    font-family: AllodsWest;
    cursor: default;
}
div.answersList {
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-end;
    align-items: stretch;
    gap: var(--size-great, 1rem);
}
.answersList > button {
    min-width: 100px;
    background: url("../../FolderFrame.png") no-repeat scroll center/100% 100%;
    padding: .5rem 2rem;
    border: none;
    /*border-radius: var(--size-hard, .5rem);*/
    outline: none;
    font-family: AllodsWest;
    color: #dadada;
    cursor: pointer;
}
.answersList > button:hover {
    background: url("../../FolderFrameSelected.png") no-repeat scroll center/100% 100%;
}

div.cardsList div.controls {
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
    gap: 1rem;
}
div.cardsList div.controls div.sound input {
    opacity: 0;
}
div.cardsList div.controls div.sound:hover input {
    opacity: 1;
}